import Card from "./Card";
import styles from "../component-css/WorthOverview.module.css";
import { useContext } from "react";
import { EditContext } from "../pages/Dashboard";

export const WorthOverviewCard = ({ index, data, handleDelete }) => {
  const editMode = useContext(EditContext);

  return (
    <Card
      index={index}
      display="0"
      editMode={editMode}
      handleDelete={handleDelete}
    >
      <div className={styles.header}>
        <div>Worth Overview</div>
        {editMode && (
          <div>
            <button onClick={handleDelete}>X</button>
          </div>
        )}
      </div>
      <div className={styles.content}>
        <div className={styles.worth}>Net Worth: ${data.ltAssets+data.stAssets-data.ltLiab-data.stLiab}</div>
        <div className={styles.indivAccounts}>
          <div className={styles.account}>
            Long Term Assets: ${data.ltAssets}
          </div>
          <div className={styles.account}>
            Short Term Assets: ${data.stAssets}
          </div>
          <div className={styles.account}>
            Long Term Liabilities: ${data.ltLiab}
          </div>
          <div className={styles.account}>
            Short Term Liabilities: ${data.stLiab}
          </div>
        </div>
      </div>
    </Card>
  );
};

export default WorthOverviewCard;
