import { signInWithPopup, GoogleAuthProvider } from "firebase/auth";
import firebase from 'firebase/app'
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { backend } from "../firebaseConfig";


const Login = ({ setAuth, setUser, auth, setData, updateAccounts }) => {
  let navigate = useNavigate();

  const signWithGoogle = async () => {
    console.log(auth)
    console.log("Signing");
    await signInWithPopup(auth, new GoogleAuthProvider())
      .then(async (result) => {
        //localStorage.setItem("currUser", JSON.stringify(result.user.accessToken));
        document.cookie = `token=${result.user.accessToken}`
        await axios
          .post(`${backend}/api/UserAuth/signOn`, {
            token: result.user.accessToken,
            signInResult: result,
          })
          .then((res) => {
            console.log(result)
            setData(res.data);
            //console.log(res.data)
            updateAccounts({
              ltAssets: res.data.balanceSheet.ltAssets,
              stAssets: res.data.balanceSheet.stAssets,
              ltLiab: res.data.balanceSheet.ltLiab,
              stLiab: res.data.balanceSheet.stLiab,
            });
          });
        setAuth(true);
        //console.log(result.user)
        setUser(result.user);

        console.log("sign");
      })
      .then(() => navigate("/finance-app/"));
  };

  return (
    <div id="loginPage">
      <p>Sign in with Google to Continue</p>
      
      <button onClick={signWithGoogle}>Sign in with Google</button>
    </div>
  );
};
export default Login;
