import styles from "../component-css/Card.module.css";
import {useContext} from 'react'

import {EditContext} from '../pages/Dashboard'

const Card = (props) => {
    const editMode = useContext(EditContext)


  let width =
    props.display == "0" ? 92.5 / 3 + "%" : (92.5 * 2) / 3 + 2.5 + "%"; // 3 wide
  let sideMargin = 0;
  let height = 0.4 * window.innerHeight;
  let topMargin = 0;
  //console.log(props.editMode)

    
  return (
    <div id={props.index} key={props.index} className={styles.card} style={{ width: width, height: height }}>
      {props.children}
    </div>
  );
};

export default Card;
