import Card from "./Card";
import CustomTooltip from "./Tooltip";

import styles from "../component-css/ProgressWidget.module.css";
import { useEffect, useState, useContext } from "react";
import { EditContext } from "../pages/Dashboard";
import ProgressBar from "./ProgressBar";

const COLORS = ["red", "blue", "green", "purple"];

export const ProgressCardSmall = ({ index, data, handleDelete }) => {
  const editMode = useContext(EditContext);
    //console.log(data)
    const progressBars = data.currExpenses.map((Expense,index)=>{
        if (Math.round(Expense.amount/data.currBudget[index].amount*100) > 0)
            return <ProgressBar key={index} bgcolor={COLORS[index % COLORS.length]} amt={Expense.amount} total={data.currBudget[index].amount} completed={Math.round(Expense.amount/data.currBudget[index].amount*100)} name={Expense.category}/>
        return
    })

  return (
    <Card
      
      index={index}
      display="0"
      editMode={editMode}
      handleDelete={handleDelete}
    >
      <div className={styles.header}>
        <div>Budget Spent</div>
        {editMode && (
          <div>
            <button onClick={handleDelete}>X</button>
          </div>
        )}
      </div>
      <div className={styles.content}>
        
        {progressBars}
      </div>
    </Card>
  );
};

export const ProgressCardLarge = () => {
  const editMode = useContext(EditContext);
};
