import {
  PieChart,
  Pie,
  Legend,
  Tooltip,
  Cell,
  ResponsiveContainer,
} from "recharts";
import Card from "./Card";
import CustomTooltip from "./Tooltip";
import styles from "../component-css/PieChart.module.css";
import { useEffect, useState,useContext } from "react";
import {EditContext} from '../pages/Dashboard'
const COLORS = ["red", "blue", "green", "purple"];



export const PieCardSmall = ({ index, data, handleDelete }) => {
  const [pieMode, setPieMode] = useState("Expenses");
  const editMode = useContext(EditContext)

  const handleSwitch = (e) => {
    e.preventDefault()
    setPieMode(e.target.getAttribute('id'))
  };

  let pieData;
  switch (pieMode) {
    case "Budget":
      pieData = data.currBudget;
      break;
    case "Expenses":
      pieData = data.currExpenses;
      break;
    default:
      pieData = [];
  }

  return (
    <Card
      index={index}
      display="0"
      editMode={editMode}
      handleDelete={handleDelete}
      
    >
      <div className={styles.header}>
        <div>{pieMode}</div>
        {editMode && (
          <div>
            <button onClick={handleDelete}>X</button>
          </div>
        )}
        {!editMode && (
          <div className={styles.switch}>
            <button onClick={handleSwitch} id="Budget">
              Budget
            </button>
            <button onClick={handleSwitch} id="Expenses">
              Expenses
            </button>
          </div>
        )}
      </div>
      <ResponsiveContainer height={"80%"}>
        <PieChart
          width={"50%"}
          height={"50%"}
          margin={{ top: 0, right: 0, bottom: 37.5, left: 0 }}
        >
          <Pie
            data={pieData}
            color="#efefef"
            dataKey="amount"
            nameKey="category"
            cx="50%"
            cy="50%"
            outerRadius={"75%"}
            innerRadius={"30%"}
            fill="#8884d8"
          >
            {pieData.map((entry, index) => (
              <Cell
                key={`cell-${index}`}
                fill={COLORS[index % COLORS.length]}
              />
            ))}
          </Pie>
          <Tooltip content={<CustomTooltip />} />
          {pieData.length <= 3 && <Legend />}
        </PieChart>
      </ResponsiveContainer>
    </Card>
  );
};

