import { useEffect, useState } from 'react';
import styles from '../css-modules/Account.module.css';
import axios from 'axios';
import { backend } from '../firebaseConfig';

export const Account = ({ showAccount }) => {
	const [profile, setProfile] = useState({});
	const [loaded, toggleLoaded] = useState(false);

	useEffect(() => {
		const authToken =
			document.cookie.split(';')[0].split('=')[1] === undefined
				? ''
				: document.cookie.split(';')[0].split('=')[1];

		axios
			.post(`${backend}/api/users/getProfile`, {
				token: authToken,
			})
			.then((res) => {
				console.log(res.data);
				setProfile(res.data);
			})
			.then(() => {
				toggleLoaded(true);
			});

		toggleLoaded(true);
	}, []);

	const handleSaveProfile = (e) => {
		e.preventDefault();
		const inputs = {};
		let inputArray = [].slice
			.call(e.target.parentElement.querySelectorAll(`.${styles.accountInput}`))
			.forEach((input) => {
				inputs[input.getAttribute('id')] = input.value;
			});
		console.log('input', inputs);

		const authToken =
			document.cookie.split(';')[0].split('=')[1] === undefined
				? ''
				: document.cookie.split(';')[0].split('=')[1];

		axios.post(`${backend}/api/users/saveProfile`, { token: authToken, profile: inputs });
		showAccount(false);
	};

	return (
		<div id={styles.accountPage}>
			<div id={styles.accountContainer}>
				<div id={styles.accountHeader}>
					<h3>Account</h3>
					<button
						className={styles.close}
						onClick={() => {
							showAccount(false);
						}}
					>
						X
					</button>
				</div>
				{loaded && (
					<div id={styles.accountProfile}>
						Username
						<input
							id="displayName"
							className={styles.accountInput}
							type="text"
							defaultValue={profile.displayName}
						/>
						<input
							id="email"
							className={styles.accountInput}
							type="email"
							defaultValue={profile.email}
							disabled
						/>
						<input type="submit" onClick={handleSaveProfile} />
					</div>
				)}
			</div>
		</div>
	);
};
