import styles from "../component-css/FilterDropdown.module.css";
import { useState, useEffect } from "react";

export const FilterDropdown = ({
  handleFilter,
  handleCheck,
  handleSort,
  filter,
  loadedCards,
}) => {

  const [open, toggleOpen] = useState(null);
  const [ecos, setEcos] = useState([]);

  const handleSubmit = (e) => {
    e.preventDefault();
    handleFilter(filter);
    toggleOpen(!open);
  };

  useEffect(() => {
    let temp = new Set();
    loadedCards.forEach((card) => {
      temp.add(card.props.eco);
    });
    setEcos([...temp]);
  }, []);

  return (
    <div>
      <button
        onClick={() => {
          toggleOpen(!open);
        }}
      >
        Filter
      </button>
      {
        <div hidden={!open} className={styles.filterContainer}>
          <div id="categories">
            <label>Categories</label>
            {ecos &&
              ecos.map((eco) => (
                <div>
                  <input
                    id={eco}
                    type="checkbox"
                    onChange={handleCheck}
                  ></input>
                  <label htmlFor={eco}>{eco}</label>
                </div>
              ))}
          </div>
          <div id="maxFee">
            <div>
              <label htmlFor="maxFeeNum">Max Fee</label>
              <input id="maxFeeNum" type="number" onChange={handleCheck} />
            </div>
          </div>

          <div>
            <label>Sort By</label>
            <div>
              <input id="NameA" name="sortBy" value="Name A-z" type="radio" onChange={handleSort}/>
              <label htmlFor="NameA">Name A-z</label>
            </div>
            <div>
              <input id="NameZ" name="sortBy" value="Name Z-a" type="radio" onChange={handleSort}/>
              <label htmlFor="NameZ">Name Z-a</label>
            </div>
            <div>
              <input id="Cost" name="sortBy" value="Cost" type="radio" onChange={handleSort}/>
              <label htmlFor="Cost">Cost</label>
            </div>
          </div>

          <button onClick={handleSubmit}>Apply</button>
        </div>
      }
    </div>
  );
};
