import styles from '../component-css/Tooltip.module.css'
const CustomTooltip = ({active,payload,label}) => {
    if (active) {
        return (
            <div className={styles.customTooltip}>
                <label>{`${payload[0].name} : \$${payload[0].value}`}</label>
            </div>
        )
    }
    return null
}
export default CustomTooltip