import { useEffect } from "react";
import styles from "../css-modules/Cards.module.css";

export const MyCards = ({ loadedCards, displayCards, userCards }) => {
  
  
  if (true) {
    

    return (
      <div className={styles.cardContainer}>
        {displayCards
          ? displayCards.filter((card) => {
              
              return userCards.includes(card.key);
            })
          : loadedCards.filter((card) => {
              
              return userCards.includes(card.key);
            })}
      </div>
    );
  }
};
