import styles from "../css-modules/Cards.module.css";
import { CreditCard } from "../components/CreditCard";
import { useEffect, useState } from "react";
import axios from "axios";
import { FilterDropdown } from "../components/FilterDropdown";
import { AllCards } from "../components/AllCards";
import { MyCards } from "../components/MyCards";
import { backend } from "../firebaseConfig";

const filter = {
  eco: new Set(),
  maxFee: Math.inf,
};

const Cards = ({ data, isAuth }) => {
  const [loaded, toggleLoaded] = useState(false);
  const [searchInput, setSearchInput] = useState("");
  const [filterInput, setFilterInput] = useState({});
  const [sortBy,setSortBy] = useState(null)
  const [filterHasChanged, toggleFilterChange] = useState(false);
  const [userCards, setUserCards] = useState(data ? [...data.userCards] : []);
  //let userCards = data ? [...data.userCards] : []
  const [loadedCards, setCards] = useState([]);
  const [displayCards, setDisplay] = useState(null);

  const [tab, changeTab] = useState("allCards");

  useEffect(() => {
    console.log(data)
    axios.post(`${backend}/api/public/creditCards`, {}).then((res) => {
      setCards(
        res.data.map((item) => {
          return (
            <CreditCard
              key={item.name}
              name={item.name}
              img={item.img}
              rewards={item.rewards}
              fee={item.fee}
              eco={item.eco}
              handleAdd={handleAdd}
              handleRemove={handleRemove}
            />
          );
        })
      );
      setDisplay(null);
      toggleLoaded(true);
    });
  }, [data]);

  const handleSearch = (e) => {
    e.preventDefault();
    setSearchInput(e.target.value);
  };

  const handleFilter = (e) => {
    console.log("filtered", filter);
    setFilterInput(filter);
    toggleFilterChange(!filterHasChanged);
  };

  useEffect(() => {
    let temp = loadedCards;
    if (searchInput.length > 0) {
      temp = temp.filter((card) => {
        return card.key
          .toLocaleUpperCase()
          .match(searchInput.toLocaleUpperCase());
      });
    }
    // Filter by Ecosystem
    if (filter.eco.size > 0) {
      temp = temp.filter((card) => {
        console.log();
        return filter.eco.has(card.props.eco);
      });
    }
    // Filter by fee
    if (filter.maxFee !== Math.inf) {
      temp = temp.filter((card) => {
        return card.props.fee <= filter.maxFee;
      });
    }
    // Filter by category

    // Sort by
    if (sortBy) {
      switch(sortBy) {
        case 'Name A-z':
          temp = temp.sort((a,b)=>{return (a.key.localeCompare(b.key))})
          break;
        case 'Name Z-a':
          temp = temp.sort((a,b)=>{return -1*(a.key.localeCompare(b.key))})
          break;
        case 'Cost':
          temp = temp.sort((a,b)=>{return a.props.fee-b.props.fee})
          break;
        default:
          break
      }
      
    }


    setDisplay(temp);
  }, [loadedCards,searchInput,filterInput,sortBy,filterHasChanged]);

  const handleSort = (e) => {
    let value = e.target.value
    setSortBy(value)
  }

  const handleCheck = (e) => {
    let value = e.target.getAttribute("id");
    switch (e.target.parentElement.parentElement.getAttribute("id")) {
      case "categories":
        if (e.target.checked) {
          filter.eco.add(value);
        } else {
          filter.eco.delete(value);
        }
        break;
      case "maxFee":
        if (!e.target.value || e.target.value < 0) filter.maxFee = Math.inf;
        else filter.maxFee = Number(e.target.value);
        break;
      default:
        break;
    }
  };

  const handleTab = (e) => {
    e.preventDefault();
    changeTab(e.target.getAttribute("id"));
  };

  const handleAdd = (e) => {
    e.preventDefault();

    //toggleLoaded(false);
    console.log("old", userCards);
    const newCard = e.target.parentElement.getAttribute("name");
    axios.post(`${backend}/api/users/creditCards/addCard`, {
      token:
        document.cookie.split(";")[0].split("=")[1] === undefined
          ? ""
          : document.cookie.split(";")[0].split("=")[1],

      userCards: userCards,
      newCard: newCard,
    });
    if (!userCards.includes(newCard)) {
      userCards.push(newCard);
      
    }
    //toggleLoaded(true)
  };

  const handleRemove = (e) => {
    e.preventDefault();
    
    toggleLoaded(!loaded)
    const deleted = e.target.parentElement.getAttribute("name");
    console.log('remove',deleted)
    axios.post(`${backend}/api/users/creditCards/removeCard`, {
      token:
        document.cookie.split(";")[0].split("=")[1] === undefined
          ? ""
          : document.cookie.split(";")[0].split("=")[1],

      userCards: userCards,
      newCard: deleted,
    });

    if (userCards.includes(deleted)) {
      userCards.splice(userCards.indexOf(deleted),1)
      console.log(userCards)
    }
    console.log('new',userCards)
    toggleLoaded(!loaded)
    
  };

  useEffect(()=>{console.log('reload')},[userCards])

  return (
    <div id={styles.cards}>
      <div id={styles.container}>
        <div className={styles.header}>
          <h2>Cards</h2>
          <div>
            {isAuth && <button id="allCards" onClick={handleTab}>
              All Cards
            </button>}
            {isAuth && <button id="myCards" onClick={handleTab}>
              My Cards
            </button>}
          </div>
        </div>

        <div className={styles.content}>
          <div className={styles.searchCards}>
            <div className={styles.searchBar}>
              <input
                id="searchField"
                onChange={handleSearch}
                value={searchInput}
                placeholder="Search Cards"
              />
            </div>
            {loaded && (
              <FilterDropdown
                handleFilter={handleFilter}
                handleCheck={handleCheck}
                handleSort={handleSort}
                filter={filter}
                loadedCards={loadedCards}
              />
            )}
          </div>
          {loaded && tab === "allCards" && (
            <AllCards loadedCards={loadedCards} displayCards={displayCards} />
          )}
          {loaded && isAuth &&  tab === "myCards" && (
            <MyCards
              loadedCards={loadedCards}
              displayCards={displayCards}
              userCards={userCards}
            />
          )}
        </div>
      </div>
    </div>
  );
};

export default Cards;
