import styles from "../css-modules/Menu.module.css";
import { Link, NavLink } from "react-router-dom";
import { signOut } from "firebase/auth";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { backend } from "../firebaseConfig";

const Menu = ({ isAuth, setAuth, setUser, auth, data, setData, accounts, renderAccount }) => {
	let navigate = useNavigate();

	const signUserOut = async (e) => {
		e.preventDefault();
		signOut(auth);
		console.log(data);
		const authToken =
			document.cookie.split(";")[0].split("=")[1] === undefined
				? ""
				: document.cookie.split(";")[0].split("=")[1];
		await axios
			.post(`${backend}/api/UserAuth/signOut`, {
				token: authToken,
			})
			.catch((err) => {});
		console.log("signed out");
		localStorage.clear();
		document.cookie = "token=";
		setAuth(false);
		setUser(null);
		setData(null);
		navigate("/finance-app/");
	};

	const print = (e) => {
		console.log("clicked");
	};

	return (
		<div id={styles.menu}>
			{data ? (
				<div id={styles.profile}>
					<img onClick={renderAccount} src={data.photoURL}></img>

					{/* <div id={styles.info}>
            <h4>{data.displayName}</h4>
            <p>{`$${
              accounts.ltAssets +
              accounts.stAssets -
              accounts.ltLiab -
              accounts.stLiab
            }`}</p>
          </div> */}
				</div>
			) : (
				<div id={styles.profile}></div>
			)}
			<nav>
				<ul>
					<li>
						<NavLink
							className={({ isActive, isPending }) =>
								(isActive ? styles.activeLink : isPending ? "" : "") +
								" " +
								styles.navLink
							}
							to="/finance-app/"
							end
						>
							Dashboard
						</NavLink>
					</li>
					<li>
						<NavLink
							className={({ isActive, isPending }) =>
								(isActive ? styles.activeLink : isPending ? "" : "") +
								" " +
								styles.navLink
							}
							to="/finance-app/Cards"
						>
							Cards
						</NavLink>
					</li>
					<li>
						<NavLink
							className={({ isActive, isPending }) =>
								(isActive ? styles.activeLink : isPending ? "" : "") +
								" " +
								styles.navLink
							}
							to="/finance-app/Budget"
						>
							Budget
						</NavLink>
					</li>
					<li>
						<NavLink
							className={({ isActive, isPending }) =>
								(isActive ? styles.activeLink : isPending ? "" : "") +
								" " +
								styles.navLink
							}
							to="/finance-app/Finances"
						>
							Finances
						</NavLink>
					</li>
					<li>
						<NavLink
							className={({ isActive, isPending }) =>
								(isActive ? styles.activeLink : isPending ? "" : "") +
								" " +
								styles.navLink
							}
							to="/finance-app/Worth"
						>
							Worth
						</NavLink>
					</li>
					{isAuth && (
						<li>
							<button className={styles.navButton} onClick={signUserOut}>
								Logout
							</button>
						</li>
					)}
					{!isAuth && (
						<li>
							<NavLink
								className={({ isActive, isPending }) =>
									(isActive ? styles.activeLink : isPending ? "" : "") +
									" " +
									styles.navLink
								}
								to="/finance-app/Login"
							>
								Login
							</NavLink>
						</li>
					)}
				</ul>
			</nav>
		</div>
	);
};

export default Menu;
