import { useState } from "react";

const ProgressBar = (props) => {
  const { bgcolor, completed, name, amt, total } = props;
  const [hover, setHover] = useState(false);

  const containerStyles = {
    height: "7.5%",
    paddingLeft: "2.5%",
    paddingRight: "2.5%",
    borderRadius: 50,
    margin: "2.5%",
  };

  const fillerStyles = {
    height: "100%",
    width: `${completed}%`,
    backgroundColor: bgcolor,
    borderRadius: "inherit",
    textAlign: "right",
  };

  const labelStyles = {
    padding: 5,
    color: "white",
    fontWeight: "bold",
  };

  const nameStyles = {
    textAlign: "left",
    marginLeft: 10,
  };

  return (
    <div style={containerStyles}>
      <div style={nameStyles}>{name}</div>
      <div
        style={fillerStyles}
        onMouseEnter={() => {
          setHover(true);
        }}
        onMouseLeave={() => {
          setHover(false);
        }}
      >
        <span style={labelStyles}>
          {completed > 10 && completed < 50
            ? `${completed}%`
            : completed >= 50
            ? !hover
              ? `${completed}%`
              : `$${amt} out of $${total}`
            : ""}
        </span>
      </div>
    </div>
  );
};

export default ProgressBar;
