import styles from "../component-css/CreditCard.module.css";

export const CreditCard = ({ img, name, fee, rewards, eco, categories, handleAdd, handleRemove }) => {
  const rewardsHTML = rewards.slice(0,4).map((reward) => (
    <div className={styles.rewardDiv}><h2>{reward.mult}</h2><h5>{reward.desc}</h5></div>
  ));

  return (
    <div id={name} eco={eco} cat={categories} className={styles.card}>
      <div className={styles.header}>
        <img src={img}></img>
        <div className={styles.info}>
          <h2>{name}</h2>
          <h3>Annual Fee: ${fee}</h3>
          <div name={name}><button onClick={handleAdd}>Add</button><button onClick={handleRemove}>Remove</button></div>
        </div>
      </div>
      <div className={styles.footer}>{rewardsHTML}</div>
    </div>
  );
};
